import {
  AUTH_REQUEST,
  AUTH_ERROR,
  AUTH_SUCCESS,
  AUTH_LOGOUT
} from "../actions/auth";

//import { USER_REQUEST } from "../actions/user";
import axios from "axios";
//import apiCall from "@/services/api//UserApi";

const state = {
  token: localStorage.getItem("user-token") || "",
  status: "",
  userName: localStorage.getItem("user-name") || "",
  hasLoadedOnce: false
};

const getters = {
  isAuthenticated: state => !!state.token,
  authStatus: state => state.status
};

const querystring = require("querystring");

const actions = {
  [AUTH_REQUEST]: ({ commit, dispatch }, user) => {
    return new Promise((resolve, reject) => {
      commit(AUTH_REQUEST);
      //alert("before axios");
      axios({
        url: "http://lab01.multicomnet.local:8080/api/login",
        data: querystring.stringify(user),
        method: "post",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      })
        .then(resp => {
          localStorage.setItem("user-token", resp.data.SessionToken);
          localStorage.setItem("user-name", resp.data.FullName);

          if (resp.data.SessionToken)
            localStorage.setItem("authenticated", "true");

          commit(AUTH_SUCCESS, resp.data);
          //dispatch(USER_REQUEST);
          resolve(resp.data);
        })
        .catch(err => {
          commit(AUTH_ERROR, err);
          localStorage.removeItem("user-token");
          localStorage.removeItem("user-name");
          reject(err);
        });
    });
  },
  [AUTH_LOGOUT]: ({ commit, dispatch }) => {
    return new Promise((resolve, reject) => {
      commit(AUTH_LOGOUT);
      localStorage.removeItem("user-token");
      localStorage.removeItem("user-name");
      localStorage.setItem("authenticated", "false");
      resolve();
    });
  }
};

const mutations = {
  [AUTH_REQUEST]: state => {
    state.status = "loading";
  },
  [AUTH_SUCCESS]: (state, resp) => {
    state.status = "success";
    state.token = resp.SessionToken;
    state.userName = resp.FullName;
    state.hasLoadedOnce = true;
  },
  [AUTH_ERROR]: state => {
    state.status = "error";
    state.hasLoadedOnce = true;
  },
  [AUTH_LOGOUT]: state => {
    state.token = "";
  }
};

export default {
  state,
  getters,
  actions,
  mutations
};
