import {
  getTransportadoras,
  getTransportadorasErrors
} from "../actions/transportadora";

import axios from "axios";
import apiCall from "@/services/api/ApiNotas";

const state = {
  listaTransportadora: "",
  errorGetTransportadora: ""
};

const getters = {
  lista: state => state.listaTransportadora
};

const querystring = require("querystring");

const actions = {
  [getTransportadoras]: ({ commit, dispatch }, token) => {
    return new Promise((resolve, reject) => {
      //alert("before axios");
      apiCall
        .getTransportadoras(token)
        .then(resp => {
          //commit(getTransportadoras, resp.data);
          resolve(resp);
        })
        .catch(err => {
          commit(getTransportadorasErrors, err);
          reject(err);
        });
    });
  }
};

const mutations = {
  [getTransportadoras]: (state, resp) => {
    state.listaTransportadora = resp.data;
  },
  [getTransportadorasErrors]: (state, err) => {
    state.errorGetTransportadora = err.message;
  }
};

export default {
  state,
  getters,
  actions,
  mutations
};
