import { getNotas, getErrorNotas } from "../actions/notas";
import apiCall from "@/services/api/ApiNotas";

import axios from "axios";

const state = {
  listaNotas: "",
  errorNotas: ""
};

const getters = {
  listaNotas: state => state.listaNotas
};

const actions = {
  [getNotas]: ({ commit, dispatch }, params) => {
    return new Promise((resolve, reject) => {
      apiCall
        .getNotas(params)
        .then(resp => {
          commit(getNotas, resp);
          resolve(resp);
        })
        .catch(err => {
          commit(getErrorNotas, err);
          reject(err);
        });
    });
  }
};

const mutations = {
  [getNotas]: (state, resp) => {
    state.listaNotas = resp.Resultado;
  },
  [getErrorNotas]: (state, err) => {
    state.errorNotas = err.message;
  }
};

export default {
  state,
  getters,
  actions,
  mutations
};
