import "expose-loader?jQuery!jquery"; // eslint-disable-line
import "expose-loader?$!jquery"; // eslint-disable-line
import axios from "axios";

function getTotayDate(dias = null) {
  var today = new Date();
  if (dias != null) {
    today.setDate(today.getDate() + dias);
  }
  var dd = String(today.getDate()).padStart(2, "0");
  var mm = String(today.getMonth() + 1).padStart(2, "0"); //January is 0!
  var yyyy = today.getFullYear();

  return yyyy + "/" + mm + "/" + dd;
}

function getTotayDateJSFormat(dias = null) {
  var today = new Date();
  if (dias != null) {
    today.setDate(today.getDate() + dias);
  }
  var dd = String(today.getDate()).padStart(2, "0");
  var mm = String(today.getMonth() + 1).padStart(2, "0"); //January is 0!
  var yyyy = today.getFullYear();

  return yyyy + "-" + mm + "-" + dd;
}

function verificaStatusFinalizador(codOcoren) {
  var statusFinalizador = ["01", "02", "25", "91"];

  return statusFinalizador.includes(codOcoren);
}

function DateDiffInHours(date1, date2) {
  return Math.floor((new Date(date2) - new Date(date1)) / (1000 * 60 * 60));
}

function DateDiffInDays(date1, date2) {
  return Math.floor(
    (new Date(date2) - new Date(date1)) / (1000 * 60 * 60 * 24)
  );
}

function transformDate(date) {
  var data = new Date(date);

  var dd = String(data.getDate()).padStart(2, "0");
  var mm = String(data.getMonth() + 1).padStart(2, "0"); //January is 0!
  var yyyy = data.getFullYear();

  return yyyy + "-" + mm + "-" + dd;
}

const querystring = require("querystring");

export default {
  async getNotas(params) {
    if (!params.DataInicial) {
      params.DataInicial = getTotayDate();
    }

    if (!params.DataFinal) {
      params.DataFinal = getTotayDate(7);
    }

    params.DataInicial = transformDate(params.DataInicial);
    params.DataFinal = transformDate(params.DataFinal);

    const response = await axios({
      url: "http://lab01.multicomnet.local:8080/api/dashboard/list",
      data: querystring.stringify(params),
      method: "post",
      headers: {
        "Content-Type": "application/x-www-form-urlencoded"
      }
    });

    return response.data;
  },

  async getTransportadoras(authToken) {
    const response = await axios({
      url: "http://lab01.multicomnet.local:8080/api/dashboard/carriers",
      data: querystring.stringify(authToken),
      method: "post",
      headers: {
        "Content-Type": "application/x-www-form-urlencoded"
      }
    });

    var optionsList = new Array();

    response.data.CarriersCode.forEach(e => {
      if (e.CarrierCodeId) {
        optionsList.push({
          value: e.CarrierCodeId,
          text: e.CarrierName + "-" + e.CodeName
        });
      }
    });

    return optionsList;
  },

  getTotalEmitidas(data) {
    var totalEmtidias = new Array();

    data.Resultado.forEach(i => {
      if (i.NotaNumero) {
        totalEmtidias.push(i);
      }
    });

    return totalEmtidias;
  },

  getTotalNotifisTrafegados(data) {
    var trafegados = new Array();

    data.Resultado.forEach(i => {
      if (i.NotifisNumero) {
        trafegados.push(i);
      }
    });

    return trafegados;
  },

  getTotalConembTrafegados(data) {
    var trafegados = new Array();

    data.Resultado.forEach(i => {
      if (i.ConembNumero) {
        trafegados.push(i);
      }
    });

    return trafegados;
  },

  getTotalEmTransito(data) {
    var emTransito = new Array();

    data.Resultado.forEach(i => {
      if (
        i.OcorenUltimoCodigo != null &&
        !verificaStatusFinalizador(i.OcorenUltimoCodigo)
      ) {
        emTransito.push(i);
      }
    });

    return emTransito;
  },

  getTotalNfsSemNotifis(data) {
    var trafegados = new Array();

    data.Resultado.forEach(i => {
      if (
        i.NotaDataRecebimento != null &&
        i.NotifisNumero == null &&
        DateDiffInHours(new Date(i.NotaDataRecebimento), new Date()) > 23
      ) {
        trafegados.push(i);
      }
    });

    return trafegados;
  },

  getTotalNotifisSemNfs(data) {
    var trafegados = new Array();

    data.Resultado.forEach(i => {
      if (
        i.NotaDataRecebimento != null &&
        i.NotaNumero == null &&
        DateDiffInHours(new Date(i.NotaDataRecebimento), new Date()) > 23
      ) {
        trafegados.push(i);
      }
    });

    return trafegados;
  },

  getTotalOcorenAcima24h(data) {
    var trafegados = new Array();

    data.Resultado.forEach(i => {
      if (
        i.NotaNumero != null &&
        i.OcorenUltimoCodigo != null &&
        verificaStatusFinalizador(i.OcorenUltimoCodigo) &&
        i.OcorenUltimaDataRecebimento != null &&
        i.OcorenUltimaDataOcorrencia != null &&
        DateDiffInHours(
          i.OcorenUltimaDataOcorrencia,
          i.OcorenUltimaDataRecebimento
        ) > 23
      ) {
        trafegados.push(i);
      }
    });

    return trafegados;
  },

  getTotalNfsSemConmb(data) {
    var trafegados = new Array();

    data.Resultado.forEach(i => {
      if (
        i.NotaNumero != null &&
        i.OcorenUltimoCodigo != null &&
        verificaStatusFinalizador(i.OcorenUltimoCodigo) &&
        (i.ConembDataRecebimento == null ||
          (i.OcorenUltimaDataRecebimento != null &&
            i.ConembDataRecebimento != null &&
            DateDiffInHours(
              i.ConembDataRecebimento,
              i.OcorenUltimaDataGeracao
            ) > 23))
      ) {
        trafegados.push(i);
      }
    });

    return trafegados;
  },

  getTotalNfsFinalizadas(data) {
    var trafegados = new Array();

    data.Resultado.forEach(i => {
      if (
        i.NotaNumero != null &&
        i.OcorenUltimoCodigo != null &&
        verificaStatusFinalizador(i.OcorenUltimoCodigo)
      ) {
        trafegados.push(i);
      }
    });

    return trafegados;
  },

  getTotalNfsFinalizadasSemInicio(data) {
    var trafegados = new Array();

    data.Resultado.forEach(i => {
      if (
        i.NotaNumero != null &&
        i.OcorenUltimoCodigo != null &&
        verificaStatusFinalizador(i.OcorenUltimoCodigo)
      ) {
        trafegados.push(i);
      }

      //TODO: fazer uma verificação chamando uma api com o numero da nota, para pegar o historico e verificar se ela não teve status de inicio 00
    });

    return trafegados;
  },

  getTotalSemOcorenMais3Dias(data) {
    var totalEmtidias = new Array();

    data.Resultado.forEach(i => {
      if (
        i.NotaNumero &&
        i.OcorenUltimoCodigo == null &&
        i.NotaDataRecebimento != null &&
        DateDiffInDays(i.NotaDataRecebimento, getTotayDateJSFormat()) > 3
      ) {
        totalEmtidias.push(i);
      }
    });

    return totalEmtidias;
  },

  getTotalNotifisSemNota(data) {
    var trafegados = new Array();

    data.Resultado.forEach(i => {
      if (i.NotifisNumero && i.NotaNumero == null) {
        trafegados.push(i);
      }
    });

    return trafegados;
  },

  getTotalFinalizadasForaPrazo(data) {
    var trafegados = new Array();

    data.Resultado.forEach(i => {
      if (
        i.NotaNumero != null &&
        i.OcorenUltimoCodigo != null &&
        verificaStatusFinalizador(i.OcorenUltimoCodigo) &&
        i.NotaDataPrevisao != null &&
        DateDiffInDays(getTotayDateJSFormat(), i.NotaDataPrevisao) > 0
      ) {
        trafegados.push(i);
      }
    });

    return trafegados;
  }
};
