<template>
  <b-container fluid>
    <b-row>
      <b-col md="4" class="my-1">
        <b-form-group class="mb-0">
          <b-input-group>
            <b-form-input v-model="filter" placeholder="Filtrar"></b-form-input>
            <b-input-group-append>
              <b-button :disabled="!filter" @click="filter = ''">Limpar</b-button>
            </b-input-group-append>
          </b-input-group>
        </b-form-group>
      </b-col>
      <b-col md="8" class="my-1">
        <div class="float-right">
          <export-excel
            class="btn-success excel-button"
            :worksheet="titlePages"
            :name="titlePages"
            :fields="json_fields"
            :data="items"
          ></export-excel>
        </div>
      </b-col>
    </b-row>
    <b-table
      small
      bordered
      responsive
      :items="items"
      :fields="fields"
      :filter="filter"
      :per-page="perPage"
      :current-page="currentPage"
      style="white-space: nowrap; background-color: #fff;"
    >
      <template slot="nota" slot-scope="props">{{props.item.NotaNumero}}-{{props.item.NotaSerie}}</template>
    </b-table>
    <b-row>
      <b-col md="6" class="my-1">
        <b-pagination
          v-model="currentPage"
          :total-rows="totalRows"
          :per-page="perPage"
          class="my-0"
        ></b-pagination>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
export default {
  name: "tabela-notas",
  data() {
    return {
      fields: [
        { key: "Transportadora", label: "Transportadora" },
        { key: "nota", label: "Nota" },
        { key: "NotaDataRecebimento", label: "Data Recebimento Nota" },
        { key: "NotaDataGeracao", label: "Data Geração Nota" },
        { key: "NotaDataPrevisao", label: "Data Previsão Nota" },
        { key: "NotifisNumero", label: "Notifis Número" },
        { key: "NotifisDataRecebimento", label: "Data Recebimento Notifis" },
        { key: "NotifisDataGeracao", label: "Data Geração Notifis" },
        { key: "ConembNumero", label: "Conemb Número" },
        { key: "ConembDataRecebimento", label: "Data Recebimento Conemb" },
        { key: "ConembDataGeracao", label: "Data Geração Conemb" },
        { key: "OcorenUltimoCodigo", label: "Ocoren Código" },
        { key: "OcorenUltimoDescricaoCodigo", label: "Ocoren Descrição" },
        { key: "OcorenUltimaDataRecebimento", label: "Data Recebimento Ocore" },
        { key: "OcorenUltimaDataGeracao", label: "Data Geração Ocoren" },
        { key: "OcorenUltimaDataOcorrencia", label: "Data Ocorencia Ocoren" }
      ],
      filter: null,
      totalRows: 1,
      currentPage: 1,
      perPage: 10,
      json_fields: {
        Transportadora: "Transportadora",
        "Nota Número": "NotaNumero",
        "Nota Série": "NotaSerie",
        "Data Recebimento Nota": "NotaDataRecebimento",
        "Data Geração Nota": "NotaDataGeracao",
        "Data Previsão Nota": "NotaDataPrevisao",
        "Notifis Número": "NotifisNumero",
        "Data Recebimento Notifis": "NotifisDataRecebimento",
        "Data Geração Notifis": "NotifisDataGeracao",
        "Conemb Número": "ConembNumero",
        "Data Recebimento Conemb": "ConembDataRecebimento",
        "Data Geração Conemb": "ConembDataGeracao",
        "Ocoren Código": "OcorenUltimoCodigo",
        "Ocoren Descrição": "OcorenUltimoDescricaoCodigo",
        "Data Recebimento Ocore": "OcorenUltimaDataRecebimento",
        "Data Geração Ocoren": "OcorenUltimaDataGeracao",
        "Data Ocorencia Ocoren": "OcorenUltimaDataOcorrencia"
      }
    };
  },
  props: {
    items: { default: [] },
    titlePages: { default: "" }
  },
  methods: {
    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    }
  },
  mounted() {
    this.totalRows = this.items.length;
  }
};
</script>

<style src="./TabelaNotas.scss" lang="scss" scoped />