<template>
  <div>
    <!-- <b-breadcrumb>
      <b-breadcrumb-item>Home</b-breadcrumb-item>
      <b-breadcrumb-item active>Dashboard</b-breadcrumb-item>
    </b-breadcrumb>-->

    <h1 class="page-title">Dashboard</h1>
    <div class="col-12">
      <Widget title="<h5><span class='fw-semi-bold'>Filtro</span></h5>" customHeader>
        <div>
          <b-form inline @submit.prevent="loadCards">
            <div class="mb-2 mr-sm-2 mt-sm-2" style="width: 300px;">
              <multiselect
                v-model="transportadorasSelecionadas"
                :options="listaTransportador"
                label="text"
                :searchable="false"
                placeholder="Transportadoras"
                :close-on-select="false"
                :clear-on-select="false"
                :multiple="true"
                :taggable="true"
                :preserve-search="true"
                track-by="text"
                :show-labels="false"
              >
                a
                <template slot="selection" slot-scope="{ values, search, isOpen }">
                  <span
                    class="multiselect__single"
                    v-if="values.length &amp;&amp; !isOpen"
                  >{{ values.length }} Transportadora(as)</span>
                </template>
                <template slot="tag">
                  <span></span>
                </template>
              </multiselect>
            </div>
            <!-- <b-form-select
              class="mb-2 mr-sm-2 mt-sm-2"
              v-model="transportadorasSelecionadas"
              :options="listaTransportador"
            >
              <template slot="first">
                <option :value="null" disabled>Selecione uma Transportadora</option>
              </template>
            </b-form-select>-->
            <label class="sr-only" for="dataInicio">Data Inicial</label>
            <v-date-picker color="blue" v-model="dataInicio" class="flex-grow">
              <b-input
                id="dataInicio"
                class="mb-2 mr-sm-2 mt-sm-2"
                placeholder="Data Inicial"
                slot-scope="{inputProps, inputEvents}"
                v-bind="inputProps"
                v-on="inputEvents"
              ></b-input>
            </v-date-picker>
            <label class="sr-only" for="dataFinal">Data Final</label>
            <v-date-picker v-model="dataFinal" class="flex-grow">
              <b-input
                id="dataFinal"
                class="mb-2 mr-sm-2 mt-sm-2"
                placeholder="Data Final"
                slot-scope="{inputProps, inputEvents}"
                v-bind="inputProps"
                v-on="inputEvents"
              ></b-input>
            </v-date-picker>
            <b-button type="submit" variant="primary">Pesquisar</b-button>
          </b-form>
        </div>
      </Widget>
    </div>
    <div class="col-12">
      <Widget
        title="<h5><span class='fw-semi-bold'>Ciclo de vida Nota Fiscal </span></h5>"
        close
        customHeader
      >
        <section v-if="loading">
          <b-row>
            <b-col></b-col>
            <b-col cols="12" md="auto">
              <Circle2 size="100px" background="#0086cc"></Circle2>
            </b-col>
            <b-col></b-col>
          </b-row>
        </section>
        <section v-else>
          <b-row>
            <b-col lg="3" sm="6" xs="12">
              <div id="sum_box" class="pb-xlg h-100" v-b-modal.modalNotasEmitidas>
                <status_card
                  message="Total de notas Emitidas"
                  icone="fa fa-file"
                  color="#9dc3e6"
                  :value="totalNotasEmitidas.length"
                  help="Total de notas Emitidas"
                ></status_card>
              </div>
            </b-col>
            <b-col lg="3" sm="6" xs="12">
              <div id="sum_box" class="pb-xlg h-100" v-b-modal.modalTotalComNotifis>
                <status_card
                  message="Total com Notifis"
                  icone="fa fa-search"
                  color="#f8cbad"
                  :value="totalComNotifis.length"
                  help="Total de notas com Notifis"
                ></status_card>
              </div>
            </b-col>
            <b-col lg="3" sm="6" xs="12">
              <div id="sum_box" class="pb-xlg h-100" v-b-modal.modalTotalComCTE>
                <status_card
                  message="Total com CTE"
                  icone="fa fa-files-o"
                  color="#adb9ca"
                  :value="totalComCTE.length"
                  help="Total com CTE"
                ></status_card>
              </div>
            </b-col>
            <b-col lg="3" sm="6" xs="12">
              <div id="sum_box" class="pb-xlg h-100" v-b-modal.modalTotalEmTransito>
                <status_card
                  message="Total em Trânsito"
                  icone="fa fa fa-truck"
                  color="#ffd966"
                  :value="totalEmTransito.length"
                  help="Total em Trânsito"
                ></status_card>
              </div>
            </b-col>
            <b-col lg="3" sm="6" xs="12">
              <div id="sum_box" class="pb-xlg h-100" v-b-modal.modalTotalFinalizadas>
                <status_card
                  message="Total Finalizadas"
                  icone="fa fa-check"
                  color="#35ba9b"
                  :value="totalFinalizadas.length"
                  help="Total Finalizadas"
                ></status_card>
              </div>
            </b-col>
            <b-col lg="3" sm="6" xs="12">
              <div id="sum_box" class="pb-xlg h-100" v-b-modal.modalTotalAtrasadas>
                <status_card
                  message="Total Atrasadas"
                  icone="fa fa-clock-o"
                  color="#ed7d31"
                  :value="totalAtrasadas.length"
                  help="Total Atrasadas"
                ></status_card>
              </div>
            </b-col>
            <b-col lg="3" sm="6" xs="12">
              <div id="sum_box" class="pb-xlg h-100" v-b-modal.modalTotalComConemb>
                <status_card
                  message="Total com CONEMB"
                  icone="fa fa-bullseye"
                  color="#c5e0b4"
                  :value="totalcomConemb.length"
                  help="Total com CONEMB"
                ></status_card>
              </div>
            </b-col>
          </b-row>
        </section>
      </Widget>
    </div>
    <div class="col-12">
      <Widget
        title="<h5><span class='fw-semi-bold'>Problemas e Anomalias</span></h5>"
        close
        customHeader
      >
        <section v-if="loading">
          <b-row>
            <b-col></b-col>
            <b-col cols="12" md="auto">
              <Circle2 size="100px" background="#0086cc"></Circle2>
            </b-col>
            <b-col></b-col>
          </b-row>
        </section>
        <section v-else>
          <b-row>
            <b-col lg="3" sm="6" xs="12">
              <div id="sum_box" class="pb-xlg h-100" v-b-modal.modalNotasSemNotifis>
                <error_card
                  message="Notas sem Notifis"
                  icone="fa fa-file"
                  color="#ef5150"
                  :value="totalNotasSemNotifis.length"
                  :totalValue="totalNotasEmitidas.length"
                  help="Notas sem Notifis"
                ></error_card>
              </div>
            </b-col>
            <b-col lg="3" sm="6" xs="12">
              <div id="sum_box" class="pb-xlg h-100" v-b-modal.modalNotasSemConemb>
                <error_card
                  message="Notas sem Conemb"
                  icone="fa fa-file"
                  color="#ef5150"
                  :value="totalNotasSemConemb.length"
                  :totalValue="totalNotasEmitidas.length"
                  help="Notas sem Conemb"
                ></error_card>
              </div>
            </b-col>
            <b-col lg="3" sm="6" xs="12">
              <div id="sum_box" class="pb-xlg h-100" v-b-modal.modalFinalizadasSemInicio>
                <error_card
                  message="Notas finalizadas sem início"
                  icone="fa fa-file"
                  color="#ef5150"
                  :value="totalNotasSemConemb.length"
                  :totalValue="totalFinalizadas.length"
                  help="Notas finalizadas sem início"
                ></error_card>
              </div>
            </b-col>
            <b-col lg="3" sm="6" xs="12">
              <div id="sum_box" class="pb-xlg h-100" v-b-modal.modalFinalizadasForaDoPrazo>
                <error_card
                  message="Notas finalizadas fora do prazo"
                  icone="fa fa-file"
                  color="#ef5150"
                  :value="totalFinalizadasForaPrazo.length"
                  :totalValue="totalFinalizadas.length"
                  help="Notas finalizadas fora do prazo"
                ></error_card>
              </div>
            </b-col>
            <b-col lg="3" sm="6" xs="12">
              <div id="sum_box" class="pb-xlg h-100" v-b-modal.modalSemOcorenMais3Dias>
                <error_card
                  message="Notas sem Ocoren a mais de 3 dias"
                  icone="fa fa-file"
                  color="#ef5150"
                  :value="totalNotasSemOcorenMais3Dias.length"
                  :totalValue="totalNotasEmitidas.length"
                  help="Notas sem Ocoren a mais de 3 dias"
                ></error_card>
              </div>
            </b-col>
            <b-col lg="3" sm="6" xs="12">
              <div id="sum_box" class="pb-xlg h-100" v-b-modal.modalNotifisSemNota>
                <error_card
                  message="Notifis sem Notas"
                  icone="fa fa-file"
                  color="#ef5150"
                  :value="totalNotifisSemNota.length"
                  :totalValue="totalComNotifis.length"
                  help="Notifis sem Notas"
                ></error_card>
              </div>
            </b-col>
            <b-col lg="3" sm="6" xs="12">
              <div id="sum_box" class="pb-xlg h-100" v-b-modal.modalOcorenAcima24h>
                <error_card
                  message="Ocoren acima de 24 horas"
                  icone="fa fa-file"
                  color="#ef5150"
                  :value="totalNotasSemConemb.length"
                  :totalValue="totalNotasEmitidas.length"
                  help="Ocoren acima de 24 horas"
                ></error_card>
              </div>
            </b-col>
          </b-row>
        </section>
        <!-- <section v-if="apiError">{{errorObject}}</section> -->
      </Widget>
    </div>
    <b-modal id="modalNotasEmitidas" size="xl" title="Total Emitidas" hide-footer>
      <table_notas :items="totalNotasEmitidas" titlePages="Total Emitidas"></table_notas>
    </b-modal>
    <b-modal id="modalTotalComNotifis" size="xl" title="Total com Notifis" hide-footer>
      <table_notas :items="totalComNotifis" titlePages="Total com Notifis"></table_notas>
    </b-modal>
    <b-modal id="modalTotalComCTE" size="xl" title="Total com CTE" hide-footer>
      <table_notas :items="totalComCTE" titlePages="Total com CTE"></table_notas>
    </b-modal>
    <b-modal id="modalTotalEmTransito" size="xl" title="Total em Transito" hide-footer>
      <table_notas :items="totalEmTransito" titlePages="Total em Transito"></table_notas>
    </b-modal>
    <b-modal id="modalTotalFinalizadas" size="xl" title="Total Finalizadas" hide-footer>
      <table_notas :items="totalFinalizadas" titlePages="Total Finalizadas"></table_notas>
    </b-modal>
    <b-modal id="modalTotalAtrasadas" size="xl" title="Total Atrasadas" hide-footer>
      <table_notas :items="totalAtrasadas" titlePages="Total Atrasadas"></table_notas>
    </b-modal>
    <b-modal id="modalTotalComConemb" size="xl" title="Total com Conemb" hide-footer>
      <table_notas :items="totalcomConemb" titlePages="Total com Conemb"></table_notas>
    </b-modal>
    <b-modal id="modalNotasSemNotifis" size="xl" title="Notas sem Notifis" hide-footer>
      <table_notas :items="totalNotasSemNotifis" titlePages="Notas sem Notifis"></table_notas>
    </b-modal>
    <b-modal id="modalNotasSemConemb" size="xl" title="Notas sem Conemb" hide-footer>
      <table_notas :items="totalNotasSemConemb" titlePages="Notas sem Conemb"></table_notas>
    </b-modal>
    <b-modal
      id="modalFinalizadasSemInicio"
      size="xl"
      title="Notas finalizadas sem início"
      hide-footer
    >
      <table_notas :items="[]"></table_notas>
    </b-modal>
    <b-modal
      id="modalFinalizadasForaDoPrazo"
      size="xl"
      title="Notas finalizadas fora do prazo"
      hide-footer
    >
      <table_notas :items="totalFinalizadasForaPrazo"></table_notas>
    </b-modal>
    <b-modal
      id="modalSemOcorenMais3Dias"
      size="xl"
      title="Notas sem Ocoren a mais de 3 dias"
      hide-footer
    >
      <table_notas :items="totalNotasSemOcorenMais3Dias"></table_notas>
    </b-modal>
    <b-modal id="modalNotifisSemNota" size="xl" title="Notifis sem Notas" hide-footer>
      <table_notas :items="totalNotifisSemNota"></table_notas>
    </b-modal>
    <b-modal id="modalOcorenAcima24h" size="xl" title="Ocoren acima de 24 horas" hide-footer>
      <table_notas :items="[]"></table_notas>
    </b-modal>
  </div>
</template>

<script>
//import $ from "jquery";
/* eslint-disable */
import "imports-loader?jQuery=jquery,this=>window!flot";
import "imports-loader?jQuery=jquery,this=>window!flot/jquery.flot.pie";
import "vue-multiselect/dist/vue-multiselect.min.css";
/* eslint-enable */
import Widget from "@/components/Widget/Widget";
import status_card from "@/components/Card/status-card";
import table_notas from "@/components/TabelaNotas/TabelaNotas";
import NotaApi from "@/services/api/ApiNotas";
import Circle2 from "../../../node_modules/vue-loading-spinner/src/components/Circle2";
import error_card from "@/components/Card/error-card";
import { getTransportadoras } from "@/store/actions/transportadora";
import { getNotas } from "@/store/actions/notas";
// import multi from "@/components/Multiselect/multi";
import Multiselect from "vue-multiselect";
// import { mapState, mapActions } from "vuex";
//import d3 from "d3";

export default {
  name: "Dashboard",
  components: {
    Widget,
    status_card,
    Circle2,
    error_card,
    table_notas,
    Multiselect
  },
  data() {
    return {
      notas: null,
      loading: true,
      apiError: false,
      errorObject: null,
      totalNotasEmitidas: [],
      totalComNotifis: [],
      totalComCTE: [],
      totalEmTransito: [],
      totalFinalizadas: [],
      totalAtrasadas: [],
      totalcomConemb: [],
      totalNotasSemNotifis: [],
      totalNotasSemConemb: [],
      totalNotasSemOcorenMais3Dias: [],
      totalNotifisSemNota: [],
      totalFinalizadasForaPrazo: [],
      dataInicio: null,
      dataFinal: null,
      token: localStorage.getItem("user-token"),
      listaTransportador: null,
      transportadorasSelecionadas: null
    };
  },
  methods: {
    loadTrasportadora() {
      // this.listaTransportador = [
      //   { teste: "Testando" },
      //   { teste2: "Testando2" }
      // ];

      this.$store
        .dispatch(getTransportadoras, { AuthToken: this.token })
        .then(transp => {
          this.listaTransportador = transp;
        })
        .catch(error => {
          alert(error);
          this.apiError = true;
          this.errorObject = error.message;
        });
    },
    loadCards() {
      this.loading = true;
      this.apiError = false;

      this.$store
        .dispatch(getNotas, {
          AuthToken: this.token,
          CarrierCodes: this.listaTransportadorasSelecionadas,
          DataInicial: this.dataInicio,
          DataFinal: this.dataFinal
        })
        .then(nts => {
          this.notas = nts;
          this.totalNotasEmitidas = NotaApi.getTotalEmitidas(nts);
          this.totalComNotifis = NotaApi.getTotalNotifisTrafegados(nts);
          this.totalComCTE = NotaApi.getTotalEmitidas(nts);
          this.totalEmTransito = NotaApi.getTotalEmTransito(nts);
          this.totalFinalizadas = NotaApi.getTotalNfsFinalizadas(nts);
          this.totalAtrasadas = NotaApi.getTotalOcorenAcima24h(nts);
          this.totalcomConemb = NotaApi.getTotalConembTrafegados(nts);
          this.totalNotasSemNotifis = NotaApi.getTotalNfsSemNotifis(nts);
          this.totalNotasSemConemb = NotaApi.getTotalNfsSemConmb(nts);
          this.totalNotasSemOcorenMais3Dias = NotaApi.getTotalSemOcorenMais3Dias(
            nts
          );
          this.totalNotifisSemNota = NotaApi.getTotalNotifisSemNota(nts);
          this.totalFinalizadasForaPrazo = NotaApi.getTotalFinalizadasForaPrazo(
            nts
          );
        })
        .catch(error => {
          this.apiError = true;
          this.errorObject = error.message;
        })
        .finally(() => (this.loading = false));
    }
  },
  computed: {
    listaTransportadorasSelecionadas: function() {
      var lista = new Array();

      if (this.transportadorasSelecionadas) {
        this.transportadorasSelecionadas.forEach(e => {
          lista.push(e.value);
        });
      }

      return lista;
    }
  },
  mounted() {
    this.loadTrasportadora();
    this.loadCards();
    //window.addEventListener("resize", this.initChart);
  },
  filters: {
    currencydecimal(value) {
      return value.toFixed(2);
    }
  }
};
</script>

<style src="./Dashboard.scss" lang="scss" scoped />


